.create--product--main--container{
  display: flex;
  justify-content: center;
  font-family: "Oswald", "Roboto";
  color: var(--white);
  user-select: none;
  background-color: transparent;
}

.form--container{
  display: flex;
  flex-direction: column;
  color: white;
}

.form--options{
  margin-top: 6rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

/* BUTTONS */

.create--product--buttons{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem;
}

.admin--product--buttons {  
  font-family: 'Oswald', 'Roboto';
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 10rem;
  min-width: 10rem;
  min-height: 2.5rem;
  color: var(--white);
  background-color: var(--black);
  padding: 0.5em;
  border-radius: 5px;
  font-weight: bold;
  border: 2px solid var(--white);
  transition: all .3s;
  margin: 0.5rem;
}

.admin--product--buttons:hover {
  filter: brightness(150%);
  border: 2px solid transparent;
  color: var(--black);
  background-color: var(--white);
  cursor: pointer;
  transition: .3s;

}