.edit--movie--general{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Oswald", "Roboto";
  color: var(--white);
  user-select: none;
background-color: transparent;
}

.edit--movie--main--container{
  margin-top: 6rem;
  display: flex;
  justify-content: center;
}

.form--cards--inner--container{
  display: flex;
  flex-direction: row;
}

.cards--container{
  margin-left: 2rem;
}

.cards{
  
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
}

/* OPTIONS IN FORM */

.create--movie--radio{
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0.5rem;
}

.create--movie--form--titles{
  pointer-events:none;
  margin-bottom: 1rem;
  margin-top: 1rem;
  margin-left: 1rem;
}

.radio--text{
  pointer-events:none;
  margin-left: 1rem;
}

.admin--select {
  font-family: "Oswald", "Roboto";
  border-radius: 0.6rem;
  text-indent: 0.5rem;
  min-height: 2rem;
}

.selected--options{
  font-family: "Oswald", "Roboto";
  border-radius: 0.6rem;
  text-indent: 0.5rem;
  min-height: 2rem;
  margin-left: 1rem;
  margin-top: 1rem;
}

.delete--container{
  display: flex;
  justify-content: center;
  align-items: center;
}

.delete--selected{
  display: flex;
}

.delete--selected{
}

.delete-btn{
  font-family: "Oswald", "Roboto";
  color: var(--white);
  background-color: var(--black);
  padding: 0.5em;
  border-radius: 5px;
  font-weight: bold;
  border: 2px solid var(--white);
  transition: all .3s;
  margin: 0.5rem;
  max-width: 1.5rem;
  margin-bottom: -1rem;
}

.delete-btn:hover{
  color: var(--black);
  background-color: var(--white);
  cursor: pointer;
  transition: .3s;
}

.admin--input {
  font-family: "Oswald", "Roboto";
  border-radius: 0.6rem;
  text-indent: 0.5rem;
  min-height: 2rem;
}

.admin--textarea {
  font-family: "Oswald", "Roboto";
  border-radius: 0.6rem;
  text-indent: 0.5rem;
  min-height: 2rem;
}

/* BUTTONS */

.edit--movie--buttons{
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 1rem;
}

.admin--buttons {  
  font-family: 'Oswald', 'Roboto';
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-width: 10rem;
  min-height: 2.5rem;
  color: var(--white);
  background-color: var(--black);
  padding: 0.5em;
  border-radius: 5px;
  font-weight: bold;
  border: 2px solid var(--white);
  transition: all .3s;
  margin: 0.5rem;
}

.admin--buttons:hover {
  filter: brightness(150%);
  border: 2px solid transparent;
  color: var(--black);
  background-color: var(--white);
  cursor: pointer;
  transition: .3s;

}