
.create--product--main--container{
  margin-top: 6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Oswald", "Roboto";
  color: var(--white);
}

.product--card--container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 100%;

}

.product--card{  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 100vw;
}

.buttons--div{
  display: flex;
  justify-content:space-evenly;
  align-items: center;
}


.deactivate--button{
  color: white;
  border: none;
  border-radius: 5px;
  padding: 0.5rem;
  cursor: pointer;
  max-width:50%;
}

.deactivate--button:hover{
  background-color: #ff0000;
}

.activate--button{
  color:  white;
  border: none;
  border-radius: 5px;
  padding: 0.5rem;
  cursor: pointer;
  max-width:50%;
}
.activate--button:hover{
  background-color: #00ff00;
}


/* BUTTONS */

.admin--product--buttons--container{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem;
}

.admin--product--buttons {  
  font-family: 'Oswald', 'Roboto';
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 10rem;
  min-width: 10rem;
  max-height: 2.5rem;
  min-height: 2.5rem;
  color: var(--white);
  background-color: var(--black);
  padding: 0.5em;
  border-radius: 5px;
  font-weight: bold;
  border: 2px solid var(--white);
  transition: all .3s;
  margin: 0.5rem;
}

.admin--product--buttons:hover {
  filter: brightness(150%);
  border: 2px solid transparent;
  color: var(--black);
  background-color: var(--white);
  cursor: pointer;
  transition: .3s;

}