@import "../../../index.css";

.admin--menu--header{
  margin-top: 6rem;
}

  .admin--menu--container{  
    display: flex;
    justify-content: center;
    font-family: "Oswald", "Roboto";
    color: var(--white);
    user-select: none;
    background-color: transparent;
  }
  
  .admin--menu{
    min-width: 70%;
    min-height: 70vh;
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  .admin--menu--options{
    display: flex;
    align-items: center;
    flex-direction: column;
  }

.admin--buttons {  font-family: 'Oswald', 'Roboto';
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-width: 10rem;
  min-height: 2.5rem;
  color: var(--white);
  background-color: var(--black);
  padding: 0.5em;
  border-radius: 5px;
  font-weight: bold;
  border: 2px solid var(--white);
  transition: all .3s;
  margin: 0.5rem;
}

.admin--buttons:hover {
  filter: brightness(150%);
  border: 2px solid transparent;
  color: var(--black);
  background-color: var(--white);
  cursor: pointer;
  transition: .3s;

}
