
.delete--movie--container{
  display: flex;
  justify-content: center;
  font-family: "Oswald", "Roboto";
  color: var(--white);
  user-select: none;
  background-color: transparent;
}

.delete--movie--inner--container{
  margin-top: 6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 100vw;
}

.cards{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
  margin-bottom: 5rem;
}


.movie--container{
  margin-right: 1rem;
  margin-left: 1rem;
}


.movie--active{
  
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 22rem;
  border-style: solid;
  border-color: rgba(0,255,0, 0.5);
  padding: 0.5rem;
  border-radius: 5px;
}

.movie--inactive{
  
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 22rem;
  border-style: solid;
  border-style: solid;
  border-color: rgba(255,0,0, 0.3);
  padding: 0.5rem;
  border-radius: 5px;
}



.movie--img{
  width: 20rem;
  height: 30rem;
  border-radius: 0.5rem;
}

.buttons--div{
  display: flex;
  justify-content:space-evenly;
  align-items: center;
}

.deactivate--button{
  color: white;
  border: none;
  border-radius: 5px;
  padding: 0.5rem;
  cursor: pointer;
  max-width:50%;
}

.deactivate--button:hover{
  background-color: #ff0000;
}

.activate--button:hover{
  background-color: #00ff00;
}

.activate--button{
  color:  white;
  border: none;
  border-radius: 5px;
  padding: 0.5rem;
  cursor: pointer;
  max-width:50%;
}

.delete--movie--container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-width: 100%;
}

.searchBar--container{
  display:flex;
  justify-content: center;
  align-items: center;
}

.delete--movie--input--button{
  display: flex;
  flex-direction: column;
}

.delete--movie--input{
  min-height: 2rem;
  min-height: 3rem;
  text-align: center;
}

.delete--movie--button{
  color:white;
  font-size: 2rem;
  font-weight: 800;
  min-height: 2rem;
  margin-left: 0.5rem;
}

.product--card{
  border-style: solid;
  border-color: white;
  border-radius: 5px;
}

/* BUTTONS */

.edit--movie--buttons{
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 1rem;
}

.admin--buttons {  
  font-family: 'Oswald', 'Roboto';
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-width: 10rem;
  min-height: 2.5rem;
  color: var(--white);
  background-color: var(--black);
  padding: 0.5em;
  border-radius: 5px;
  font-weight: bold;
  border: 2px solid var(--white);
  transition: all .3s;
  margin: 0.5rem;
}

.admin--buttons:hover {
  filter: brightness(150%);
  border: 2px solid transparent;
  color: var(--black);
  background-color: var(--white);
  cursor: pointer;
  transition: .3s;

}