
.searchbar-container{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.admin--input {
  font-family: "Oswald", "Roboto";
  border-radius: 0.6rem;
  text-indent: 0.5rem;
  min-height: 2rem;
}

.admin--search--buttons {  
  font-family: 'Oswald', 'Roboto';
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 4rem;
  min-height: 2.5rem;
  color: var(--white);
  background-color: var(--black);
  padding: 0.5em;
  border-radius: 5px;
  font-weight: bold;
  border: 2px solid var(--white);
  transition: all .3s;
  margin: 0.5rem;
}

.admin--search--buttons:hover {
  filter: brightness(150%);
  border: 2px solid transparent;
  color: var(--black);
  background-color: var(--white);
  cursor: pointer;
  transition: .3s;

}